.App {
  text-align: center;
  display: flex;
  margin: 10px;
}
body {
  background-color: #cdcdcd;
}
.container {
  display: grid;
  margin: 30px 40vh;
  grid-template-columns: 110px 110px 110px;
  grid-template-rows: 120px 120px 120px;
}
.block {
  height: 100px;
  width: 100px;
  background-color: #255f5f;
  font-size: 75px;
  text-align: center;
  color: black;
  border-radius: 20px;
}
h1 {
  text-align: center;
}